import { Component } from '@angular/core';
import { ActionSheetController  ,AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
@IonicPage()
@Component({
  selector: 'page-solicitudessun-list',
  templateUrl: 'solicitudessun-list.html',
})
export class SolicitudessunListPage {


  public loading: boolean = true;
  items: any=[];
  items2: any=[];
    generated = '';
    public hasFilter: boolean = false;
    idempresa:any;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  idafiliado:any='na';
  public gSearchQuery: string = '';
  username: string = "";
//private barcodeScanner:BarcodeScanner,
  constructor(public actionSheetCtrl: ActionSheetController,public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth


    ) {

      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value

    })

  }

  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  showFilter3()
  {

    let prompt = this.alertCtrl.create({
      title:' Eliminar asignación',
      message: 'Ingrese NOV/ carné',
      inputs: [
        {
          name: 'NOV',
          placeholder: 'Ingrese NOV/ Carnét'

        }
      ],
      buttons: [
        {
          text: 'Cancelar'
        },
        {
          text: 'Crear',
          handler: todo => {

                if(todo){

                  this.regService.getReglibreSext2('1/'+todo.NOV + '/existepcb','asignapcbs').then((data) => {


                  let alert = this.alertCtrl.create({ title:'La asignación a eliminar es de '+ data[0].nombre + ', Esta seguro de realizar esta operación?',
                  buttons: [
                    {text: 'Si',
                      handler: () => {

                        this.regService.getReg2b('1/2/'+todo.NOV,'asignapcbs').then((data) => {
                        //  this.items=data;

                          let recordID 		: string		=data[0]._id;
                          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'asignapcbs').then((data) => {
                            this.msj.showError3('Eliminación','Registro eliminado con exito');
                            }, (err) => {
                              this.msj.showError3('Error',err.error);
                          });

                    }, (err) => {
                      this.msj.showError3('Error',err.error);
                    });





                      }
                    },
                    {
                      text: 'No',  handler: () => { }
                    }
                  ]
                });
                alert.present();


              });


                }


          }
        }
      ]
    });

    prompt.present();

  }

  showFilter2()
  {


    let prompt = this.alertCtrl.create({
      title:' Creacion de nuevo NOV/ Carnét para poder asignar',
      message: '',
      inputs: [
        {
          name: 'NOV',
          placeholder: 'Ingrese NOV/ Carnét'

        },
        {

          name: 'NOMBRE',
          placeholder: 'Ingrese Nombre'
        }
      ],
      buttons: [
        {
          text: 'Cancelar'
        },
        {
          text: 'Crear',
          handler: todo => {

                if(todo){

                  let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación? <br> Nombre:'+ todo.NOMBRE + ' <br> Nov:'+ todo.NOV,
                  buttons: [
                    {text: 'Si',
                      handler: () => {


                  let creat='Crea';

                  let   options       : any	     = {
                   carnet : todo.NOV,
                  nombre : todo.NOMBRE,
                  bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' creacion de nov o carnet ' +  todo.NOV}
                 };



                 this.regService.createRegS2ext('',options,'estudiantevt').then((result) => {

                   this.msj.showError3('Mensaje','Registro creado con exito');
                   }, (err) => {

                     this.msj.showError3('Mensaje',err.error);
                   });




                      }
                    },
                    {
                      text: 'No',  handler: () => { }
                    }
                  ]
                });
                alert.present();







                }


          }
        }
      ]
    });

    prompt.present();

  }

  showFilter4()
  {


    let actionSheet = this.actionSheetCtrl.create({
      title: 'Descarga de reportes',
      buttons: [
        {
          text: 'Tabla de asignaciones Campus',

          handler: () => {
            this.regService.abrelinkinterno('datosfijos/tablaasignacion');
          }
        },
        {
          text: 'Tabla de asignaciones Centros',

          handler: () => {
            this.regService.abrelinkinterno('datosfijos/tablaasignacioncentro');
          }
        },
        {
          text: 'Tabla info boleta',
          handler: () => {
            this.regService.abrelinkinterno('datosfijos/tablainfoboleta');

          }
        }
      ]
    });
    actionSheet.present();


  }
  pideregistros() {
      let self = this;
      self.loading = true;
      this.regService.getReg('todos','nuevosalons').then((data) => {
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }
  ionViewDidLoad() {
    this.pideregistros();
  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {

          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'nuevosalons').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });

        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();



  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('SolicitudessunCreatePage', { record : item },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('SolicitudessunCreatePage',{ record : null },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
      }
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }

   viewRecord(item : any): void
   {
     // let modal = this._MODAL.create(ViewGalleryPage, { record : item });
     // modal.present();
   }

   excludeTracks: any = [];


   showFilter()
   {

     let self = this;
     self.loading = true;

   let actionSheet = this.actionSheetCtrl.create({
     title: 'Filtrar por estado:',
     buttons: [
       {
         text: 'Procesado',
         handler: () => {

           this.items=this.items2
           this.items= this.items2.filter((v) => {

             if(v.estado ) {

               if (v.estado=='Procesado') {
                 return true;
               }
               return false;

             }
           });
           self.loading = false;

           this.hasFilter = true;
         }
       },
       {
         text: 'Solicitando',
         handler: () => {
           this.items=this.items2
           this.items= this.items2.filter((v) => {
             if(v.estado ) {
               self.loading = false;
               if (v.estado=='Solicitando') {
                 return true;
               }
               return false;

             }
           });
         }
       }

     ]
   });

   actionSheet.present();



   }

}



